.catalog-table-list {
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 20px 20px 0;
  box-sizing: border-box;
  border-radius: 0 0 4px 4px;
  border-top: none;

  @media (min-width: @tablet-width) {
    margin: 59px 0 0;
    padding: 0 0 0 40px;
  }
}

.catalog-table-list__label {
  display: block;
  font-family: "Blogger Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 22px;
  letter-spacing: 0;
  text-align: left;
  color: @base-grey;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.catalog-table-list__checkbox:checked ~ .catalog-table-list__label {
  background-color: @base-yellow;
}
